.header{ 
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}
.header-bg{
    background-color: rbg(0,0,0,0.85);
    transition: 0.5s;
}

.nav-menu{
    display: flex;
}

.nav-menu li{
    padding: 0 1em;
}


.nav-menu a{
    font-size: 1.2 rem;
    font-weight: 500;
}

.hamburger{
    display: none;
}

/* small screen display */
@media screen and (max-width: 1040px){
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background-color: #000;
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.3s;
    }

    .nav-menu.active{
        left: 0;
    }

    .nav-menu li{
        padding: 1em 0;
    }
    
    .nav-menu li a {
        font-size: 2rem;
    }

    .hamburger{
        display: initial;
    }
}