.mask{
    width: 100%;
    height: 100vh;
    position: relative;

}
.intro-img { 
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after{ 
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0.8;
}

.hero { 
    height: 100%;
    width: 100%;
}

.hero .content {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}
.hero .content h1 {
    font-size: 4pm;
    padding: 0.6em 0 1.5rem;
}


.hero .content p {
    font-size: 1.4pm;
    font-weight: 200; 
    text-transform: uppercase;
}

.content .btn {
    margin: 1rem 0.2rem;
}

@media screen and (max-width: 640px){
    
    .hero .content h1 {
        font-size: 3rem;
    }


    .hero .content p {
        font-size: 1.4rem;
    }

}